<template>
    <div class="setComponent">
        <div class="form-item">
            <label>卡片列数</label>
            <el-select v-model="obj.columns" clearable placeholder="请选择">
                <el-option
                    v-for="item in columnOptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
        </div>   
        <div class="form-item">
            <label>增加卡片</label>
            <el-button type="primary" round size="mini" @click="addClick">增加</el-button>
        </div>
        <div class="form-item">
            <label>删除卡片</label>
            <el-button :disabled="obj.imgTextBox.length<=3" type="danger" round size="mini" @click="removeClick">删除</el-button>
        </div>      
        <div class="form-item">
            <label>标题</label>
            <el-button size="mini" @click="titleClick('title')">编辑</el-button>
        </div>
        <div>
        <div class="form-item">
            <label>描述</label>
            <el-button size="mini" @click="titleClick('desc')">编辑</el-button>
        </div>
        </div>
        <div class="form-item">
            <label>图片</label>
            <el-button size="small" type="primary" @click="uploadClick">上传图片</el-button>
        </div>

        <div class="form-item">
            <label>图片动画</label>
            <el-select v-model="obj.animateImg" clearable placeholder="请选择">
                <el-option-group
                    v-for="(group,i) in options"
                    :key="i"
                    :label="group.label">
                    <el-option
                        v-for="(item,itemIndex) in group.options"
                        :key="itemIndex"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-option-group>
            </el-select>
        </div>
    </div>
</template>
<script>
import { animateOptions } from '@/utils/animateOptions'
export default {
    name: 'ThreeColumns2',
    props: {
        obj: {
            type: Object,

        }
    },
    data() {
        return {
            options: animateOptions,
            columnOptions: [
                {label: '2列',value: 2},
                {label: '3列',value: 3},
                {label: '4列',value: 4},
                {label: '6列',value: 6},
            ]
        }
    },
    methods: {
        butClick(msg) {
            this.$emit('butClick',msg)
        },
        uploadClick() {
            this.$emit('uploadClick',this.obj)
        },
        addClick() {
            this.obj.imgTextBox.push(this.obj.imgTextBox[0])
        },
        removeClick() {
            this.obj.imgTextBox.pop()
        },
        titleClick(type) {
            this.$emit('titleClick',[this.obj,type])
        }
    }
}
</script>
<style lang="less">

</style>

